$--portfolio-color1: #50a3f0;
$--portfolio-color2: #f98e8e;
$--portfolio-color3: #5ac8ae;

$--portfolio-pie-degree-1: 90deg;
$--portfolio-pie-degree-2: 90deg;

@mixin display-flex($direction, $justify, $align) {
	display: flex;
	flex-direction: $direction;
	justify-content: $justify;
	align-items: $align;
}

.portfolio {
	grid-area: 2/1/3/2;
	padding: 1.5rem 3rem 1.5rem 3rem;
	@include display-flex(column, flex-start, center);
	gap: 1rem;

	&__head {
		@include display-flex(row, space-between, center);
		height: 20%;
		width: 100%;

		&__title {
			font-size: 1.2rem;
			font-weight: 600;
		}

		&__value {
			font-size: 0.9rem;
		}
	}

	&__data {
		@include display-flex(row, space-around, center);
		height: 80%;
		width: 100%;
		gap: 3rem;

		&__circle {
			width: 100%;
			height: 100%;
			@include display-flex(row, center, center);
			
		}

		
	}
}
@media only screen and (max-width:600px){

	.portfolio {
		// display: none;
		// grid-area: 4/1/5/2;
		position: fixed;
		width: 100vw;
		height: 43vh;
		top: 0.1rem;
		border-bottom: 0.1rem solid rgba(74, 93, 112, 0.3);
		justify-content: center;
		z-index: -10;

		&__head{
			height: 2rem;
		}

		&__data{
			height: 10rem;
		}
	}

}